
const btnSearch = document.querySelector('.header__search-btn');
const searchForm = document.querySelector('.header__search-form');
const searchBtnClose = document.querySelector('.header__search-form-close');


btnSearch.addEventListener('click', () => {
    searchForm.classList.add('active');
})

searchBtnClose.addEventListener('click', () => {
    searchForm.classList.remove('active');
})

document.addEventListener('click', e => {
    if (!e.target.closest('.header__search-btn, .header__search-form, header__search-form-input')) {
        searchForm.classList.remove('active');
    }
})

const burger = document.querySelector('.header__burger-btn');
const burgerMenu = document.querySelector('.header__links')
const burgerMenuClose = document.querySelector('.header__links-close')

burger.addEventListener('click', () => {
    burgerMenu.classList.add('active');
    document.body.classList.add('lock')
})

burgerMenuClose.addEventListener('click', () => {
    burgerMenu.classList.remove('active');
    document.body.classList.remove('lock');
})

const btnCloseAddress = document.querySelector('.contacts__container-box-contact-btn');
const menuAddress = document.querySelector('.contacts__container-box-contact');

btnCloseAddress.addEventListener('click', () => {
    menuAddress.classList.add('not-active');
})


ymaps.ready(init)
function init() {
    var myMap = new ymaps.Map('map', {
        center: [55.760220568958395,37.618560499999894],
        zoom: 13,
        controls: []
    });

    var myGeoObject = new ymaps.GeoObject({
        geometry: {
            type: "Point", // тип геометрии - точка
            coordinates: [] // координаты точки
        },
      });
      var myPlacemark = new ymaps.Placemark([55.76963601332982,37.636710499999985], {}, {
      iconLayout: 'default#image',
      iconImageHref: 'spriteMulticolor.svg#phone',
      iconImageSize: [100, 100],
      iconImageOffset: [-3, -42]
      });
      myMap.behaviors.disable('drag');
      myMap.behaviors.disable('scrollZoom');
      myMap.geoObjects.add(myPlacemark);
      myMap.controls.remove('searchControl');
      myMap.controls.remove('trafficControl');
      myMap.controls.remove('typeSelector');
      myMap.controls.remove('fullscreenControl');
      myMap.controls.remove('rulerControl');
}
